import { useState } from "react";
import Button from "./Button"; 
import ToPurchase from "./ToPurchase";

const Purchase = ({onClick}) => {

 const [state, setState] = useState(0.00)

 function upDateOnehr () {
    setState(state + 89.99)
 }

 function upDateTwohr () {
    setState(state + 174.99)
 }

 function upDateGroup () {
    setState(state + 399.99)
 }

    return (
      <div id='booklesson'>
          
          <h5>1 hr lesson: $89.99</h5> <Button onClick={upDateOnehr}></Button><br></br>
          <h5>2 hr lesson: $174.99</h5> <Button onClick={upDateTwohr}></Button><br></br>
          <h5>Group Lesson: $399.99</h5> <Button onClick={upDateGroup}></Button><br></br>
          <p id='total'>Your Total:</p><div className='inline'><h2 id='totalAmount'>${state}</h2></div>
          <ToPurchase/>
          
      </div>
    );
  }
  
  export default Purchase;